<template>
  <div class="attendance_records pages">
    <div class="header">
      <div class="flex1" @click="changeMonth('prev')">
        <img src="@/assets/imgs/houtui.png" alt="">
      </div>
      <div class="flex2">
        {{nowMonth | moment('YYYY年MM月')}}
      </div>
      <div class="flex1" @click="changeMonth('next')">
        <img src="@/assets/imgs/qianjin.png" alt="">
      </div>
    </div>
    <ul class="tabbar">
      <li :class="{active: nowItem==='plan'}" @click="toggleItem('plan')">班表</li>
      <li :class="{active: nowItem==='attendance'}" @click="toggleItem('attendance')">考勤</li>
    </ul>
    <div class="scroll_content_box">
      <div class="box">
        <div class="row" v-for="item in originData" :key="item.id">
          <div class="left">{{item.sheduling_date | moment('YYYY-MM-DD')}}</div>
          <div class="right">{{item.arrange_order_id}} {{item.time_interval}}</div>
          <div class="hr"></div>
        </div>
        <no-item v-if="originData.length===0"></no-item>
        <div class="blank" v-else></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AttendanceRecords',
  data () {
    return {
      nowMonth: new Date(),
      nowItem: 'plan',
      originData: [
        {
          id: 1,
          sheduling_date: '2018-10-11',
          arrange_order_id: '夜',
          time_interval: '18:00-06:00'
        }
      ]
    }
  },
  methods: {
    changeMonth (flag) {
      switch (flag) {
        case 'prev':
          this.nowMonth = this.$moment(this.nowMonth).subtract(1, 'month')
          break
        case 'next':
          this.nowMonth = this.$moment(this.nowMonth).add(1, 'month')
          break
        default:
          break
      }
    },
    toggleItem (item) {
      this.nowItem = item
    },
    getShedulingData () {
      this.$rpc.call('funenc_xa_station.sheduling_record', 'get_sheduling_record_by_user', [], {
        month: this.$moment(this.nowMonth).format('YYYY-MM-01')
      }).then(res => {
        this.originData = res
      }).catch(e => {
        console.log(e)
        alert('获取班表失败')
      })
    },
    getAttendanceData () {
      this.$rpc.call('funenc_xa_station.sheduling_record', 'get_sheduling_record_by_user', [], {
        month: this.$moment(this.nowMonth).format('YYYY-MM-01')
      }).then(res => {
        this.originData = res
      }).catch(e => {
        console.log(e)
        alert('获取考勤失败')
      })
    }
  },
  mounted () {
    this.nowItem = this.$route.meta.item
    this.getShedulingData()
  },
  watch: {
    nowMonth (v) {
      if (this.nowItem === 'plan') {
        this.getShedulingData()
      } else {
        this.getAttendanceData()
      }
    },
    nowItem (v) {
      if (this.nowItem === 'plan') {
        this.getShedulingData()
      } else {
        this.getAttendanceData()
      }
    }
  }
}
</script>

